.skill-box {
  background-color: rgba(255, 255, 255, 0.3); 
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  max-width: 300px; /* Ensure both boxes are of the same width */
  margin: 20px;
  max-height: 450px;
}

.skill-box h2 {
  text-align: center;
  margin-bottom: 10px; /* Ensures uniform spacing below the header */
}

.skill-box ul {
  list-style: none;
  padding: 0;
}

.skill-box li {
  margin-bottom: 10px;
  align-items: center;
  display: flex;
}

.skill-box svg {
  margin-right: 10px;
  width: 20px;
  height: auto;
}
